<template>
  <div class="app-video">
    <img id="playAndPauseInVideo" v-show="!isHidePlayAndPause"
         class="playAndPauseInVideo" :class="playAndPauseInVideoDomCss"
         @click.once="init()"
         src="~static/images/play-big.png"
         alt="">
    <img class="videoImg" v-show="isShowVideo?false:true"
         :src="video_data.img_url"
         alt="">
    <!--<video id="video" @click="stateChange()" controls preload>-->
    <video id="video" @click="isinit&&stateChange()" preload>
      <source v-if="isShowVideo" :src="video_data.video_url" type="video/mp4">
    </video>
    <div class="controls">
      <img id="playAndPauseInControls" @click="stateChange()" class="playAndPauseInControls"
           src="~static/images/play.png" alt=""/>
      <div class="time"><span id="nowtime">{{nowTime}}</span>&ensp;/&ensp;<span id="alltime">{{allTime}}</span></div>
      <img id="fullscreen" class="fullscreen" src="~static/images/fullscreen.png" alt=""
           @click="isFullScreen?exitFullscreen():launchFullScreen()"/>
      <div class="volumeWindow" @mouseover.once="initVolume()">
        <img id="volume" class="volume" src="~static/images/volume1.png" alt=""/>
        <div id="volumeBar" class="volumeBar">
          <div id="volumeRoad" class="volumeRoad">
            <div id="volumeLine" class="volumeLine">
              <div id="volumeDot" class="volumeDot"></div>
            </div>
          </div>
        </div>
      </div>
      <div class="progressBar">
        <div id="progressRoad" class="progressRoad">
          <div id="progressLine" class="progressLine">
            <div id="progressDot" class="progressDot"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app-video',
    props: ['video_data'],
    data() {
      return {
        isinit: false,//
        videoWindowDom: null,//dom
        videoDom: null,//videodom
        isShowVideo: false,//
        isHidePlayAndPause: false,//
        playAndPauseInVideoDom: null,//dom()
        playAndPauseInControlsDom: null,//dom()
        progressRoadDom: null,//dom()
        progressLineDom: null,//dom()
        progressDotDom: null,//dom()
        VolumeDOM: null,//dom
        volumeRoadDom: null,////dom()
        volumeLineDom: null,//dom()
        volumeDotDom: null,//dom()
        FullScreenDom: null,//dom
        nowTime: '00:00',//
        allTime: '00:00',//
        playAndPauseInVideoDomCss: '',//
        isFullScreen: false,//
        VolumeObj: 0.5,//()
      }
    },
    watch: {
      video_data: function (val, oldVal) {
//        console.log('video_data');
//        console.log(':');
//        console.log(val);
//        console.log(':');
//        console.log(oldVal);
      }
    },
    methods: {
      init: function () {
        let self = this;

        //DOM
        self.videoDom = document.getElementById('video');
        self.playAndPauseInVideoDom = document.getElementById('playAndPauseInVideo');
        self.playAndPauseInControlsDom = document.getElementById('playAndPauseInControls');
        self.progressRoadDom = document.getElementById('progressRoad');
        self.progressLineDom = document.getElementById('progressLine');
        self.progressDotDom = document.getElementById('progressDot');
        if (self.VolumeDOM === null)
          self.VolumeDOM = document.getElementById('volume');
        if (self.volumeRoadDom === null)
          self.volumeRoadDom = document.getElementById('volumeRoad');
        if (self.volumeLineDom === null)
          self.volumeLineDom = document.getElementById('volumeLine');


        //
        self.videoDom.onloadstart = function (ev) {//
          console.log('');
        };
        self.videoDom.onprogress = function (ev) {//
          console.log('');
        };
        self.videoDom.onloadedmetadata = function (ev) {//
          console.log('');
          console.log('' + self.videoDom.duration);
          self.allTime = self.calculation(self.videoDom.duration).rs;
        };
        self.videoDom.ontimeupdate = function (ev) {//
          self.nowTime = self.calculation(self.videoDom.currentTime).rs;
          self.progressLineDom.style.width = self.videoDom.currentTime / self.videoDom.duration * self.progressRoadDom.offsetWidth + "px";
        };
        self.videoDom.onvolumechange = function (ev) {//
          console.log('' + self.videoDom.volume);
          self.volumeLineDom.style.height = (self.videoDom.volume * self.volumeRoadDom.offsetHeight) + 'px';
          if (self.videoDom.volume === 0) {
            self.VolumeDOM.src = self.app_config.staticPath + '/images/volume0.png';
          } else {
            self.VolumeDOM.src = self.app_config.staticPath + '/images/volume1.png';
          }
        };
        self.videoDom.onwaiting = function (ev) {//
          console.log('');
        };
        self.videoDom.onended = function (ev) {//
          console.log('');
        }
        self.isShowVideo = true;//()
        self.isHidePlayAndPause = true;//
        self.playAndPauseInVideoDom.src = self.app_config.staticPath + '/images/play-big.png';//
        self.playAndPauseInControlsDom.src = self.app_config.staticPath + '/images/pause.png';//
        self.videoDom.volume = self.VolumeObj;
        console.log('' + self.videoDom.volume);
        self.videoDom.play();//
        self.isinit = true;//
        self.setProgressMouseFn();//
      },
      stateChange: function () {//
        let self = this;
        if (self.videoDom != null) {
          self.isHidePlayAndPause = false;
          self.playAndPauseInVideoDomCss = 'tobig';
          window.setTimeout(function () {
            self.isHidePlayAndPause = true;
            self.playAndPauseInVideoDomCss = '';
          }, 500);
          if (self.videoDom.paused) {
            self.playAndPauseInVideoDom.src = self.app_config.staticPath + '/images/play-big.png';
            self.playAndPauseInControlsDom.src = self.app_config.staticPath + '/images/pause.png';
            self.videoDom.play();
          } else {
            self.playAndPauseInVideoDom.src = self.app_config.staticPath + '/images/pause-big.png';
            self.playAndPauseInControlsDom.src = self.app_config.staticPath + '/images/play.png';
            self.videoDom.pause();
          }
        } else {
          self.init();
        }
      },
      calculation: function (value) {//
        let num = 0,
          m = 0,
          s = 0,
          rs = '';
        let v_int = parseInt(value);//int
        if (v_int === value) {
          num = v_int;
        } else {
          num = v_int + 1;
        }

        s = num % 60;
        m = parseInt(num / 60);

        if (m > 9) {
          rs += m + ':';
        } else {
          rs += '0' + m + ':';
        }

        if (s > 9) {
          rs += s;
        } else {
          rs += '0' + s;
        }

        return {
          num: num,
          m: m,
          s: s,
          rs: rs
        };
      },
      getPoint: function (obj) {//
        let t = obj.offsetTop; //
        let l = obj.offsetLeft; //
        //
        while (obj = obj.offsetParent) {// obj = obj.offsetParent;while (obj != undefined)
          t += obj.offsetTop; //
          l += obj.offsetLeft; //
        }
        return {
          top: t,
          left: l
        };
      },
      setProgressMouseFn: function () {//
        let self = this,
          isDrag = false,
          mouseX = 0,
          oldWidth = 0,
          fullwidth = self.progressRoadDom.offsetWidth,
          slide = false;
        self.progressDotDom.addEventListener('mousedown', function (ev) {
          isDrag = true;
          mouseX = ev.clientX;
          oldWidth = self.progressLineDom.offsetWidth;
          return false;
        });
        document.addEventListener('mousemove', function (ev) {
          if (isDrag) {
            slide = true;
            let l = ev.clientX - mouseX + oldWidth;
            if (l < 0) {
              l = 0;
            } else if (l > fullwidth) {
              l = fullwidth;
            }
            self.videoDom.currentTime = l * self.videoDom.duration / fullwidth;
          }
        });
        document.addEventListener('mouseup', function (ev) {
          if (isDrag) {
            isDrag = false;
          }
          return false;
        });
        self.progressRoadDom.addEventListener('click', function (ev) {
          if (!isDrag && !slide) {
            self.videoDom.currentTime = (ev.clientX + document.documentElement.scrollLeft - self.getPoint(self.progressRoadDom).left) * self.videoDom.duration / fullwidth;
          }
          slide = false;
        });
      },
      launchFullScreen: function () {//
        let self = this;
        if (self.videoWindowDom === null)
          self.videoWindowDom = document.getElementsByClassName('app-video')[0];
        if (self.FullScreenDom === null)
          self.FullScreenDom = document.getElementById('fullscreen');
        self.FullScreenDom.src = self.app_config.staticPath + '/images/originalscreen.png';
        self.isFullScreen = true;
        if (self.videoWindowDom.requestFullscreen) {
          return self.videoWindowDom.requestFullScreen();
        } else if (self.videoWindowDom.webkitRequestFullScreen) {
          return self.videoWindowDom.webkitRequestFullScreen();
        } else if (self.videoWindowDom.mozRequestFullScreen) {
          return self.videoWindowDom.mozRequestFullScreen();
        } else {
          return self.videoWindowDom.msRequestFullscreen();
        }
      },
      exitFullscreen: function () {//
        let self = this;
        if (self.videoWindowDom === null)
          self.videoWindowDom = document.getElementsByClassName('app-video')[0];
        if (self.FullScreenDom === null)
          self.FullScreenDom = document.getElementById('fullscreen');
        self.FullScreenDom.src = self.app_config.staticPath + '/images/fullscreen.png';
        self.isFullScreen = false;
        if (document.exitFullscreen) {
          return document.exitFullscreen();
        } else if (document.webkitExitFullscreen) {
          return document.webkitExitFullscreen();
        } else if (document.mozExitFullScreen) {
          return document.mozExitFullScreen();
        } else {
          return document.msExitFullScreen();
        }
      },
      initVolume: function () {
        let self = this;

        if (self.VolumeDOM === null)
          self.VolumeDOM = document.getElementById('volume');
        if (self.volumeRoadDom === null)
          self.volumeRoadDom = document.getElementById('volumeRoad');
        if (self.volumeLineDom === null)
          self.volumeLineDom = document.getElementById('volumeLine');
        self.volumeDotDom = document.getElementById('volumeDot');

        let isDrag = false,
          mouseY = 0,
          oldHeight = 0,
          fullheight = self.volumeRoadDom.offsetHeight,
          slide = false,
          temp = 0.5;

        self.volumeDotDom.addEventListener('mousedown', function (ev) {
          isDrag = true;
          mouseY = ev.clientY;
          oldHeight = self.volumeLineDom.offsetHeight;
          return false;
        });
        document.addEventListener('mousemove', function (ev) {
          if (isDrag) {
            slide = true;
            let l = mouseY - ev.clientY + oldHeight;
            if (l < 0) {
              l = 0;
            } else if (l > fullheight) {
              l = fullheight;
            }
            if (self.videoDom != null) {
              self.videoDom.volume = l / fullheight;
              self.VolumeObj = self.videoDom.volume;
            } else {
              temp = self.VolumeObj = l / fullheight;
              if (self.VolumeObj === 0) {
                self.VolumeDOM.src = self.app_config.staticPath + '/images/volume0.png';
              } else {
                self.VolumeDOM.src = self.app_config.staticPath + '/images/volume1.png';
              }
              self.volumeLineDom.style.height = (self.VolumeObj * self.volumeRoadDom.offsetHeight) + 'px';
            }
          }
        });
        document.addEventListener('mouseup', function (ev) {
          if (isDrag) {
            isDrag = false;
          }
          return false;
        });

        self.volumeRoadDom.addEventListener('click', function (ev) {
          if (!isDrag && !slide) {
            if (self.videoDom != null) {
              self.videoDom.volume = (fullheight - (ev.clientY + document.documentElement.scrollTop - self.getPoint(self.volumeRoadDom).top)) / fullheight;
            }
            else {
              temp = self.VolumeObj = self.videoDom.volume;
              if (self.VolumeObj === 0) {
                self.VolumeDOM.src = self.app_config.staticPath + '/images/volume0.png';
              } else {
                self.VolumeDOM.src = self.app_config.staticPath + '/images/volume1.png';
              }
              self.volumeLineDom.style.height = (self.VolumeObj * self.volumeRoadDom.offsetHeight) + 'px';
            }
          }
          slide = false;
        });

        self.VolumeDOM.onclick = function () {
          if (self.videoDom != null) {
            if (self.videoDom.volume === 0)
              self.VolumeObj === 0 ? self.videoDom.volume = self.VolumeObj = 0.5 : self.videoDom.volume = self.VolumeObj;
            else
              self.videoDom.volume = 0;
          } else {
            if (self.VolumeObj === 0)
              self.VolumeObj = temp;
            else
              self.VolumeObj = 0;
            if (self.VolumeObj === 0) {
              self.VolumeDOM.src = self.app_config.staticPath + '/images/volume0.png';
            } else {
              self.VolumeDOM.src = self.app_config.staticPath + '/images/volume1.png';
            }
            self.volumeLineDom.style.height = (self.VolumeObj * self.volumeRoadDom.offsetHeight) + 'px';
          }
        };
      }
    }
  }
</script>

<style scoped>
  .app-video {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #ddd;
  }

  .app-video > .playAndPauseInVideo {
    display: block;
    cursor: pointer;
    position: absolute;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, .3);
    top: 50%;
    left: 50%;
    margin: -45px 0 0 -45px;
    z-index: 1;
  }

  .app-video > .tobig {
    animation: big .5s linear forwards;
  }

  .app-video > .videoImg {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .app-video > video {
    display: block;
    width: 100%;
    height: 100%;
  }

  .app-video > .controls {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    background-color: rgba(0, 0, 0, .3);
    color: #fff;
  }

  .app-video > .controls > .playAndPauseInControls,
  .app-video > .controls > .volumeWindow,
  .app-video > .controls > .fullscreen {
    float: left;
    width: 30px;
    height: 30px;
    display: block;
    box-sizing: border-box;
    padding: 5px;
    cursor: pointer;
  }

  .app-video > .controls > .volumeWindow {
    padding: 0;
  }

  .app-video > .controls > .volumeWindow:hover > .volumeBar {
    visibility: visible;
  }

  .app-video > .controls > .volumeWindow > img.volume {
    display: block;
    box-sizing: border-box;
    width: 30px;
    height: 30px;
    padding: 5px;
  }

  .app-video > .controls > .time {
    float: left;
    font-size: 12px;
    width: 90px;
    text-align: center;
  }

  .app-video > .controls > .progressBar {
    position: relative;
    margin-left: 126px;
    margin-right: 76px;
    height: 30px;
  }

  .app-video > .controls > .progressBar > .progressRoad {
    position: absolute;
    top: 14px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: rgba(255, 255, 255, .3);
    cursor: pointer;
    box-sizing: border-box;
  }

  .app-video > .controls > .progressBar > .progressRoad > .progressLine {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    background-color: #4285F4;
  }

  .app-video > .controls > .progressBar > .progressRoad > .progressLine > .progressDot {
    position: absolute;
    top: -5px;
    right: -6px;
    background-color: #4285F4;
    border-radius: 50%;
    width: 12px;
    height: 12px;
    cursor: pointer;
  }

  .app-video > .controls > .volumeWindow,
  .app-video > .controls > .fullscreen {
    float: right;
    margin-left: 5px;
  }

  .app-video > .controls > .volumeWindow > .volumeBar {
    visibility: hidden;
    position: absolute;
    top: -100px;
    right: 35px;
    width: 30px;
    height: 100px;
    background-color: rgba(0, 0, 0, .3);
  }

  .app-video > .controls > .volumeWindow > .volumeBar > .volumeRoad {
    position: absolute;
    top: 12px;
    bottom: 8px;
    left: 14px;
    width: 2px;
    background-color: rgba(255, 255, 255, .3);
    cursor: pointer;
  }

  .app-video > .controls > .volumeWindow > .volumeBar > .volumeRoad > .volumeLine {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 2px;
    height: 40px;
    background-color: #4285F4;
  }

  .app-video > .controls > .volumeWindow > .volumeBar > .volumeRoad > .volumeLine > .volumeDot {
    position: absolute;
    top: -6px;
    left: -5px;
    background-color: #4285F4;
    border-radius: 50%;
    width: 12px;
    height: 12px;
    cursor: pointer;
  }

  @keyframes big {
    0% {
      width: 50px;
      height: 50px;
      margin: -25px 0 0 -25px;
      opacity: 1;
    }
    100% {
      width: 90px;
      height: 90px;
      margin: -45px 0 0 -45px;
      opacity: 0;
    }
  }
</style>



